@tailwind base;
@tailwind components;
@tailwind utilities;

/*
  Light theme revamp
  - Keep existing component classnames (stat-card, info-card, etc.) but restyle them.
  - Provide a modern “dashboard” look with subtle borders + soft shadows.
*/

:root {
  --bg: #f6f7fb;
  --card: #ffffff;
  --text: #0f172a; /* slate-900 */
  --muted: #64748b; /* slate-500 */
  --border: rgba(15, 23, 42, 0.10);
  --shadow: 0 10px 24px rgba(2, 6, 23, 0.08);
  --shadow-sm: 0 6px 16px rgba(2, 6, 23, 0.08);
  --primary: #2563eb; /* blue-600 */
  --primary-2: #7c3aed; /* violet-600 */
}

@layer base {
  body {
    background: var(--bg);
    color: var(--text);
  }
}

.app-shell {
  background: radial-gradient(1200px 600px at 10% 0%, rgba(37, 99, 235, 0.10), transparent 60%),
    radial-gradient(900px 500px at 90% 10%, rgba(124, 58, 237, 0.10), transparent 55%),
    var(--bg);
}

/* Topbar */
.topbar {
  background: rgba(255, 255, 255, 0.85);
  border-bottom: 1px solid var(--border);
  backdrop-filter: blur(10px);
}

.kicker {
  font-size: 12px;
  letter-spacing: 0.16em;
  text-transform: uppercase;
  color: var(--muted);
  font-weight: 600;
}

.page-title {
  font-size: 34px;
  line-height: 1.05;
  font-weight: 800;
  letter-spacing: -0.02em;
}

.subhead {
  font-size: 14px;
  color: var(--muted);
  margin-top: 6px;
}

.chip {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 10px 12px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.85);
  box-shadow: var(--shadow-sm);
}

.chip-text {
  font-size: 12px;
  color: var(--muted);
  font-weight: 600;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  background: rgba(100, 116, 139, 0.35);
}

.dot--on {
  background: rgba(37, 99, 235, 1);
  box-shadow: 0 0 0 6px rgba(37, 99, 235, 0.12);
  animation: dotPulse 1.2s ease-in-out infinite;
}

@keyframes dotPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

/* Navigation */
.nav-shell {
  background: rgba(246, 247, 251, 0.75);
  border-bottom: 1px solid var(--border);
  backdrop-filter: blur(10px);
}

.tabs {
  display: flex;
  gap: 10px;
  padding: 12px 0;
  overflow-x: auto;
}

.nav-tab {
  padding: 10px 14px;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: var(--muted);
  border: 1px solid transparent;
  border-radius: 999px;
  background: transparent;
  transition: all 160ms ease;
  white-space: nowrap;
}

.nav-tab:hover {
  background: rgba(15, 23, 42, 0.04);
  color: var(--text);
}

.nav-tab.active {
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.14), rgba(124, 58, 237, 0.12));
  border-color: rgba(37, 99, 235, 0.22);
  color: var(--text);
}

/* Error Banner */
.error-banner {
  background: rgba(254, 243, 199, 0.7);
  border: 1px solid rgba(251, 191, 36, 0.35);
  color: #92400e;
  padding: 14px 16px;
  margin-bottom: 18px;
  border-radius: 16px;
}

/* Stats Cards */
.stat-card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 18px;
  box-shadow: var(--shadow);
}

.stat-value {
  font-size: 38px;
  line-height: 1;
  font-weight: 800;
  letter-spacing: -0.02em;
  margin-bottom: 10px;
  background-image: linear-gradient(135deg, var(--primary) 0%, var(--primary-2) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.stat-label {
  font-size: 12px;
  color: var(--muted);
  font-weight: 700;
  letter-spacing: 0.14em;
  text-transform: uppercase;
}

/* Section Titles */
.section-title {
  font-size: 18px;
  font-weight: 800;
  letter-spacing: 0.10em;
  text-transform: uppercase;
  color: var(--text);
}

.section-title::after {
  content: '';
  display: block;
  width: 72px;
  height: 4px;
  margin-top: 12px;
  border-radius: 999px;
  background: linear-gradient(90deg, var(--primary), var(--primary-2));
}

/* Info Cards */
.info-card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 20px;
  box-shadow: var(--shadow);
}

.card-title {
  font-size: 12px;
  font-weight: 800;
  letter-spacing: 0.14em;
  text-transform: uppercase;
  color: var(--text);
  padding-bottom: 12px;
  margin-bottom: 14px;
  border-bottom: 1px solid var(--border);
}

/* Schedule Tables */
.phase-header {
  padding: 16px 18px;
  border-radius: 16px 16px 0 0;
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.95) 0%, rgba(124, 58, 237, 0.95) 100%);
}

.schedule-table {
  background: var(--card);
  border: 1px solid var(--border);
  border-top: none;
  border-radius: 0 0 16px 16px;
  overflow: hidden;
  box-shadow: var(--shadow);
}

.schedule-table thead {
  background: rgba(15, 23, 42, 0.03);
}

.schedule-table th {
  color: var(--muted);
}

.schedule-table td {
  color: var(--text);
}

.schedule-table tbody tr:hover {
  background: rgba(15, 23, 42, 0.03);
}

/* Match coloring helpers (used by renderMatchWithWinner) */
.match-winner {
  color: #166534; /* green-800 */
  font-weight: 800;
}

.match-muted {
  color: rgba(100, 116, 139, 0.95);
}

.match-scoreline {
  color: rgba(100, 116, 139, 0.85);
  font-weight: 700;
}

/* Lunch Break */
.lunch-break {
  background: rgba(251, 191, 36, 0.12);
  border: 1px solid rgba(251, 191, 36, 0.25);
  border-radius: 16px;
  padding: 26px 18px;
  box-shadow: var(--shadow-sm);
}

/* Final Match */
.final-match {
  background: var(--card);
  border: 1px solid rgba(124, 58, 237, 0.20);
  border-radius: 18px;
  padding: 30px 18px;
  box-shadow: var(--shadow);
}

.final-badge {
  display: inline-block;
  padding: 8px 12px;
  font-size: 11px;
  font-weight: 800;
  letter-spacing: 0.14em;
  text-transform: uppercase;
  color: white;
  border-radius: 999px;
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-2) 100%);
}

/* Bracket Styles */
.bracket-container {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 18px;
  box-shadow: var(--shadow);
}

.bracket-stage-title {
  font-size: 11px;
  font-weight: 800;
  letter-spacing: 0.14em;
  text-transform: uppercase;
  text-align: center;
  color: var(--muted);
  padding-bottom: 10px;
  border-bottom: 1px solid var(--border);
}

.bracket-match {
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.7);
  border-radius: 16px;
  padding: 12px;
  box-shadow: var(--shadow-sm);
}

.match-team {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  border-radius: 12px;
  background: rgba(15, 23, 42, 0.03);
}

.match-score {
  width: 52px;
  text-align: center;
  padding: 6px 8px;
  font-size: 12px;
  font-weight: 800;
  color: white;
  border-radius: 10px;
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-2) 100%);
}

/* Final Bracket Match */
.bracket-final {
  border: 2px solid rgba(124, 58, 237, 0.28);
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.10), rgba(124, 58, 237, 0.10));
  border-radius: 16px;
  padding: 16px;
  box-shadow: var(--shadow);
}

.final-trophy {
  text-align: center;
  margin-bottom: 10px;
  font-size: 28px;
  color: #7c3aed;
}

/* Consolation Matches */
.consolation-section {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 16px;
  box-shadow: var(--shadow);
}

.consolation-title {
  font-size: 11px;
  font-weight: 800;
  letter-spacing: 0.14em;
  text-transform: uppercase;
  color: var(--muted);
  margin-bottom: 14px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--border);
}

.consolation-match {
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.7);
  border-radius: 16px;
  padding: 12px;
  box-shadow: var(--shadow-sm);
}

.match-team-small {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 10px;
  border-radius: 12px;
  background: rgba(15, 23, 42, 0.03);
  font-size: 13px;
}

.match-score-small {
  width: 48px;
  text-align: center;
  padding: 5px 6px;
  font-size: 11px;
  font-weight: 800;
  color: white;
  border-radius: 10px;
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-2) 100%);
}

/* Teams / Pools */
.division-title {
  font-size: 16px;
  font-weight: 900;
  letter-spacing: 0.10em;
  text-transform: uppercase;
  color: var(--text);
}

.division-title::after {
  content: '';
  display: block;
  width: 54px;
  height: 4px;
  margin-top: 10px;
  border-radius: 999px;
  background: linear-gradient(90deg, var(--primary), var(--primary-2));
}

.pool-card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 16px;
  box-shadow: var(--shadow);
}

.pool-title {
  font-size: 11px;
  font-weight: 800;
  letter-spacing: 0.14em;
  text-transform: uppercase;
  color: var(--muted);
  padding-bottom: 10px;
  margin-bottom: 12px;
  border-bottom: 1px solid var(--border);
}

.team-input {
  width: 100%;
  padding: 12px 12px;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid var(--border);
  border-radius: 14px;
  font-size: 14px;
  color: var(--text);
  transition: all 160ms ease;
}

.team-input:hover {
  border-color: rgba(37, 99, 235, 0.30);
}

.team-input:focus {
  outline: none;
  border-color: rgba(37, 99, 235, 0.55);
  box-shadow: 0 0 0 5px rgba(37, 99, 235, 0.14);
}

.team-input::placeholder {
  color: rgba(100, 116, 139, 0.8);
}

/* Skeletons */
.skeleton-page {
  display: grid;
  gap: 14px;
}

.skeleton-grid {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 12px;
}

@media (min-width: 640px) {
  .skeleton-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (min-width: 1024px) {
  .skeleton-grid {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

.skeleton-card,
.skeleton-block {
  border-radius: 18px;
  border: 1px solid var(--border);
  background: linear-gradient(90deg, rgba(15, 23, 42, 0.05), rgba(15, 23, 42, 0.10), rgba(15, 23, 42, 0.05));
  background-size: 200% 100%;
  animation: shimmer 1.2s ease-in-out infinite;
}

.skeleton-card {
  height: 94px;
}

.skeleton-block {
  height: 160px;
}

@keyframes shimmer {
  0% { background-position: 0% 0%; }
  100% { background-position: -200% 0%; }
}

/* Scrollbar Styles */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(100, 116, 139, 0.35);
  border-radius: 999px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(100, 116, 139, 0.55);
}

/* Selection */
::selection {
  background: rgba(37, 99, 235, 0.20);
}

::-moz-selection {
  background: rgba(37, 99, 235, 0.20);
}

/* Make tables more readable on light bg */
table {
  border-collapse: separate;
  border-spacing: 0;
}
